<template>
  <div class="side-nav">
    <a-menu mode="inline" :inline-collapsed="collapsed" :openKeys="openKeys" v-model:selectedKeys="selectedKeys" @openChange="onOpenChange" @select="selectItem">
      <div v-if="route == 'ui'">
        <a-menu-item :key="'sub' + [index]" v-for="(title, index) in data2" class="padding-50">
          <router-link :to="{ name: title.name }">{{
            title.desc
          }}</router-link>
        </a-menu-item>
      </div>

      <a-sub-menu :key="'sub' + [index]" v-for="(title, index) in Object.keys(data)" v-else>
        <template #icon v-if="title == '组件'">
          <icon-font type="icon-customize" />
        </template>
        <template #icon v-else-if="title == '设计基础'">
          <icon-font type="icon-logo-small" />
        </template>
        <template #icon v-else>
          <icon-font type="icon-Information" />
        </template>
        <template #title>{{ title }}</template>

        <div v-if="title == '组件'">
          <a-menu-item-group :key="nav2.desc" :title="nav2.desc" v-for="nav2 in data[title]">
            <a-menu-item :key="nav3.desc" v-for="nav3 in nav2.items">
              <router-link :to="{ name: nav3.name }" :class="$route.name === nav3.name ? 'active' : ''">{{ nav3.desc }}</router-link>
            </a-menu-item>
          </a-menu-item-group>
        </div>
        <div v-else v-for="nav in data[title]">
          <a-menu-item :key="nav.desc" v-if="nav.name != 'index'">
            <router-link :class="$route.name === nav.name ? 'active' : ''" v-if="nav.name" :to="{ name: nav.name }">{{ nav.desc }}</router-link>
            <p v-else>{{ nav.desc }}</p>
          </a-menu-item>
        </div>
      </a-sub-menu>
    </a-menu>

  </div>




</template>
<script>
import navConf from "../nav.config.json";
import { defineComponent, reactive, toRefs, nextTick , ref} from "vue";
import { createFromIconfontCN } from "@ant-design/icons-vue";
const IconFont = createFromIconfontCN({
  scriptUrl: "//at.alicdn.com/t/font_2651879_ltjfsbfwbtf.js", // 在 iconfont.cn 上生成
});
export default {
  setup() {
    const state = reactive({
      rootSubmenuKeys: ["sub0", "sub1", "sub2", "sub3"],
      openKeys: ["sub0"],
      selectedKeys: ["介绍"],
      data: navConf,
      data2: [
        { desc: "设计原则", name: "principle", path: "/principle/ui" },
        { desc: "更新机制", name: "update", path: "/update/ui" },
      ],
    });

    const onOpenChange = (openKeys) => {
      const latestOpenKey = openKeys.find(
        (key) => state.openKeys.indexOf(key) === -1
      );
      if (state.rootSubmenuKeys.indexOf(latestOpenKey) === -1) {
        state.openKeys = openKeys;
      } else {
        state.openKeys = latestOpenKey ? [latestOpenKey] : [];
      }
    };

    return { 
      ...toRefs(state),
      onOpenChange
    };
  },
  props: {
    route: {
      type: String,
      default: "",
    },
  },
  created() {
    const that = this
    if (this.route == "ui") {
      this.selectedKeys = ["sub0"];
    }
    //保持route与左边栏目一致
    nextTick(() => {
      const itemName = this.$route.name
      let selectedKeys = [], openKeys = []
      Object.keys(navConf).map((nav, subIndex) => {
        navConf[nav].map((item, index) => {
          if (item.items) {
            item.items.map(_item => {
              if (_item.name === itemName) {
                selectedKeys = [_item.desc]
                openKeys = [`sub${subIndex}`]
              }
            })
          } else {
            if (item.name === itemName) {
              selectedKeys = [item.desc]
              openKeys = [`sub${subIndex}`]
            }
          }
          that.openKeys = openKeys
          that.selectedKeys = selectedKeys
        })

        if (this.route == "ui") {
          this.selectedKeys = ["sub0"];
        }
      })
    })
  },
  watch: {
    route() {

      if (this.route == "design") {
        this.openKeys = ["sub0"];
        this.selectedKeys = ["介绍"];
      } else if (this.route == "ui") {
        this.selectedKeys = ["sub0"];
      }
    },
  },

  components: {
    IconFont,
  },
};
</script>

<style lang="less" type="text/less">
.side-nav {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  width: 260px;
  margin-top: 65px;
  padding-bottom: 40px;
  background-color: #fff;
  overflow: hidden;
  overflow-y: auto;
  .ant-menu-inline,
  .ant-menu-vertical,
  .ant-menu-vertical-left {
    border-right: none;
  }
  .ant-menu-sub.ant-menu-inline {
    background: none;
  }
  .ant-menu-title-content {
    font-weight: 500;
    z-index: 5;
    a {
      font-weight: normal;
      width: 100%;
      height: 100%;
      float: left;
    }
  }
  .ant-menu-submenu-expand-icon,
  .ant-menu-submenu-arrow {
    color: #666666;
    font-weight: normal;
  }
  .ant-menu-vertical .ant-menu-item::after,
  .ant-menu-vertical-left .ant-menu-item::after,
  .ant-menu-vertical-right .ant-menu-item::after,
  .ant-menu-inline .ant-menu-item::after {
    border-left: 4px solid #1bbc9b;
    left: 0px;
    border-right: none;
  }
  .ant-menu-item .ant-menu-item-icon,
  .ant-menu-submenu-title .ant-menu-item-icon,
  .ant-menu-item .anticon,
  .ant-menu-submenu-title .anticon {
    font-size: 16px;
    min-width: 16px;
  }
  .ant-menu-submenu-arrow::before,
  .ant-menu-submenu-arrow::after {
    height: 1px;
  }
  .ant-menu-submenu-selected .ant-menu-submenu-arrow {
    color: #1bbc9b;
  }
  .ant-menu-item a::before {
    display: none;
  }
  .padding-50 {
    padding-left: 52px !important;
  }
}


</style>
