
<template>
  <header id="header" :class="isIndex == true ? 'index-header' : ''">
    <a-row type="flex" justify="space-between">
      <a-col :span="5">
        <router-link :to="{ path: '/img-projects' }"><img src="../assets/img/logo.png" class="logo" width="120" height="39" /></router-link>
      </a-col>
      <a-col :span="6" :offset="13">
        <a-menu v-model:selectedKeys="current" mode="horizontal" id="nav">
          <a-menu-item  v-if="hasLogined" @click="exit" key="退出登录">
            退出登录
          </a-menu-item> 
          <!-- <a-menu-item @click="toFunction(3)" key="组件"> 组件 </a-menu-item>
           <a-menu-item @click="toFunction(4)" key="文章"> 文章 </a-menu-item> -->
        </a-menu>
      </a-col>
    </a-row>
  </header>
</template>

<script>
export default {
  name: "app",
  data() {
    return {
      current: [""],
      hasLogined: false
    };
  },

  props: {
    isIndex: {
      type: Boolean,
      default: "",
    },
  },
  created() {
    if (sessionStorage.getItem("route_param")) {
      this.current = [sessionStorage.getItem("route_param")];
    }

  },

  watch: {
    $route() {
      if(sessionStorage.getItem('userId')){
        this.hasLogined= true
      }
    }
  },
  methods: {
    toFunction(key) {
      const functionMap = {
        2: "principle",
        3: "giud",
        4: "",
      };
      const typeMap = {
        2: "ui",
        3: "design",
        4: "",
      };
      this.$router.push({
        name: functionMap[key],
        params: { type: typeMap[key] },
      });
    },
    exit(){
      sessionStorage.setItem('userId', '')
      sessionStorage.setItem('token', '')
      this.hasLogined= false
      this.$router.push({
        name: 'index'
      })
    }
  },
};
</script>

<style type="text/less" lang="less" scoped>
@import "../assets/ant-less/index.less";

#header {
  position: fixed;
  width: 100%;
  background: #fff;
  box-shadow: 0px 4px 8px 0px rgba(0, 0, 0, 0.04);
  z-index: 11;
  padding: 12px 40px;
  transition: all 0.3s cubic-bezier(0.78, 0.14, 0.15, 0.86);
  .ant-menu {
    background: none;
    border-bottom: none;
    line-height: 39px;
    color: #666666;
    float: right;
  }
  .ant-menu-horizontal:not(.ant-menu-dark) > .ant-menu-item,
  .ant-menu-horizontal:not(.ant-menu-dark) > .ant-menu-submenu {
    padding: 0px 18px;
  }
  #nav ::after {
    bottom: -12px;
    right: 20px;
    left: 20px;
  }
}
#header.index-header {
  width: 1200px;
  position: absolute;
  left: 0px;
  right: 0px;
  margin-right: auto;
  margin-left: auto;
  background: 0 0;
  z-index: 10;
  height: 40px;
  box-shadow: none;
  padding: 12px 0px;
  #nav ::after {
    bottom: 0px;
  }
}
</style>