import { createStore } from 'vuex'

export default createStore({
  state: {
     ossType: '七牛云', // 七牛云 ｜ 阿里云
  },
  mutations: {
    setOssType(state, value){
      state.ossType = value
    }
  },
  actions: {
    saveOssType({commit}, data){
      commit('setOssType', data)
    }
  },
  modules: {
  }
})
