import { createRouter, createWebHistory } from 'vue-router'

import navConf from '../nav.config.json'


let routes = []

//从路由配置文件读取路由列表
Object.keys(navConf).forEach((header) => {
  routes = routes.concat(navConf[header])
})


let addComponent = (router) => {
  router.forEach((route) => {
    //递归拆解
    if (route.items) {
      route.items = route.items.map(item => {
        item.type = route.type
        return item
      })
      routes = routes.concat(route.items)
      addComponent(route.items)
    } else {
      route.component = () => import(`../pages${route.path}/index.vue`);
    }
  })
}
addComponent(routes)


const singles= [
  {
    name: 'index',
    path: '/',
    component: () => import(`../pages/index.vue`)
  },
  {
    name: 'upload',
    path: '/upload',
    component: () => import(`../pages/upload.vue`)
  },
  {
    name: 'pics',
    path: '/pics',
    component: () => import(`../pages/pics/index.vue`)
  }
]

routes = routes.concat(singles)



const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

router.beforeEach((to, from, next) => {
   if(to.name !=='index' && !sessionStorage.getItem('userId')){
     next({name: 'index'})
   }else{
     next()
   }
})

export default router
