<template>
  <a-config-provider :locale="locale">
    <div id="app">
      <div>
        <mainHeader :isIndex="isIndex"></mainHeader>
        <div class="container" v-if="!isIndex">
          <sideNav class="nav" :route="route"></sideNav>
          <router-view class="view content-container"></router-view>
        </div>
        <router-view class="page" v-else> </router-view>
        <mainFooter v-if="isIndex"></mainFooter>
      </div>
      <a-back-top />
    </div>
  </a-config-provider>

  
</template>

<script>
import mainHeader from "./components/header.vue";
import mainFooter from "./components/footer.vue";
import sideNav from "./components/side-nav.vue";



/* 展示组件中文格式 */
import zhCN from "ant-design-vue/es/locale/zh_CN";
import moment from "moment";
import "moment/locale/zh-cn";

moment.locale("zh-cn");

export default {
  name: "app",
  data() {
    return {
      init: false,
      isIndex: true,
      locale: zhCN,
      showRightNav: true,
      route: "",
    };
  },
  watch: {
    $route() {
      this.route = this.$route.params.type;
      this.isIndex = this.$route.name === "index";
    },
  },
  mounted() { 
  },
  components: {
    mainHeader,
    sideNav,
    mainFooter,
  },
};
</script>

<style lang="less" type="text/less">
@import "./assets/md/index.less";

.container {
  width: 100%;
  background-color: #fff;
  padding-top: 63px;
  .nav {
    float: left;
    width: 260px;
    background: #ffffff;
    box-shadow: 1px 0px 0px 0px #eeeeee;
  }
  .view {
    padding: 32px 40px 32px 70px;
    box-sizing: border-box;
    margin-left: 260px;
    margin-right: 190px;
  }
}

.container:after {
  content: "";
  clear: both;
  display: block;
}
div.ant-back-top-content {
  background: rgba(0, 0, 0, 0.6);
  border-radius: 4px;
  .anticon:after {
    width: 22px;
    height: 24px;
    position: absolute;
    left: 9px;
    top: 8px;
    background: url(./assets/img/back_icon.png)
      no-repeat;
    background-size: cover;
    content: "";
  }
  svg {
    display: none;
  }
}

.ant-btn{
  margin-right: 12px;
}

ul{
  li{
    list-style: none;
  }
}
</style>
